@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.movie-row {
  margin-bottom: 1.7rem;
  padding-top: 1rem;

}

.movie-row h2 {
  font-size: 1.8rem;
  margin-left: 2.5rem;
}

.movie-row-list-area{
  overflow-x: hidden;
  padding-left: 1rem;
}

.movieRow-list{
  transition: all .3s ease;
}
.movie-row-item{
  display: inline-block;
  width: 9rem;
  cursor: pointer;
  
}

.movie-row-item img{
  width: 100%;
  -webkit-transform: scale(.9);
          transform: scale(.9);
  transition: .2s ease;

}

.movie-row-item img:hover{
  -webkit-transform: scale(1);
          transform: scale(1);

}



.movie-row-right,.movie-row-left{
  position: absolute;
  width: 40px;
  height: 15rem;
  background-color: rgba(0, 0, 0, 0.609);
  z-index: 3000;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  opacity: 0;
  overflow: hidden;
  cursor: pointer;

  transition: all .3s ease;
}

.movie-row:hover .movie-row-right, .movie-row:hover .movie-row-left{
  opacity: 1;

}

.movie-row-right{
  right: 0;
}

.movie-row-left{
  left: 0;
}


@media screen and (max-width: 760px){
  .movie-row-left,
  .movie-row-right{
    opacity: 1;
  }


  .movie-row h2{
    font-size: 1.5rem;
  }
}
.featured{
  height: 100vh;

  background-size: cover;
  background-position: center;
}

.featured-vertical{
  width: inherit;
  height: inherit;
  background-image: linear-gradient(to top, #111 10%, transparent 90%);
}

.featured-horizontal{
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, #111 30%, transparent 70%);
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 2rem;
  padding-bottom: 150px;
  padding-top: 100px;
}

/* os 10% siginifca qeu so quero a cor preta em 10% da image

e 90% no restante da image*/


.featured-name{
  font-size: 3rem;
  font-weight: bold;
}

.featured-info{
  font-size: 1rem;
  font-weight: bold;
  margin-top: .7rem;

  display: flex;
  grid-gap:2rem;
  gap:2rem;
}

.featured-points{
  color: #46d369;
}

.featured-description{
  margin-top: 1rem;
  font-size: 1.1rem;
  color: #999;

  max-width: 40%;
}

.featured-buttons{
  margin-top: .9rem;
}


.featured-watchButton,
.featured-MyListButton{
    display: inline-block;
    font-size: 1.3rem;
    font-weight: bold;
    padding: .7rem 2rem;
    text-decoration: none;
    border-radius: 5px;
    margin-right: .8rem;
    transition: all .2s ease;
    opacity: 1;
}

.featured-watchButton{
  background-color: #fff;
  color:#000;

}

.featured-MyListButton{
  color: white;
  background-color: #333;
}

.featured-watchButton:hover,
.featured-MyListButton:hover{
  opacity: .8;
}

.featured-genres{
  margin-top: 1rem;
  font-size: 1.2rem;
  color: #999;
}

@media screen and (max-width: 760px){
    .featured{
      height: 90vh;
    }

    .featured-name{
      font-size: 2.5rem;
    }

    .featured-info{
      font-size: .9rem;
    }
    .featured-description{
      font-size: 1rem;
      max-width: 70%;
    }

    .featured-watchButton,
    .featured-MyListButton{
      font-size: 1rem;
      padding: .6rem 1rem;
    }

    .featured-genres{
      font-size: .8rem;
    }

}

@media screen and (max-width: 550px){
  .featured-description{
    max-width: 90%;
  }

}
header{
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  right: 0;
  height: 70px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
  background: transparent;

  transition: all .4s ease;
}

header.black{
  background-color: #141414;
}

.header-logo{
  height: 4rem;
}

.header-logo img,
.header-user img{
  height: 100%;

}

.header-user{
  height: 2.5rem;

}

.header-user img{
  border-radius: 3px;
}

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  text-decoration: none;
}

body{
  background-color:#111;
  color: white;

}

.lists{
 margin-top: -7rem;

}

footer{
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  padding-bottom: 1rem;
}

footer p,footer a{
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 300;
  color: #999;
  transition: .3s ease;
}
footer p{
  line-height: 1rem;
}

footer p a:hover{
  letter-spacing: 1.3px;
  color: #fff;
}

.loading{
  position: fixed;
  bottom: 0;
  
  background-color: #000;
  width: 100%;
  height: 100%;

  display: grid;
  place-items: center;
  transition: .4s ease;
}

.loading img{
  width: 100%;
  /* margin-left: -3rem; */

}

.loading.desconnect{
  visibility: hidden;
  opacity: 0;
}


